import axios from 'axios'
import {
  CART_ADD_ITEM,
  CART_REMOVE_ITEM,
  CART_SAVE_SHIPPING_ADDRESS,
  CART_SAVE_PAYMENT_METHOD,
} from '../constants/cartConstants'

export const addToCart = ( id, qty, option,discount ) => async ( dispatch, getState ) => {

  

  const { data } = await axios.get( `/api/products/${ id }` )
  const JSObject = JSON.parse(data.choicesObj)
  const price_list = JSObject.map( item => Object.values( item )[ 1 ] )
  const thePrice = discount == 100 ? price_list[option] : price_list[option] * (discount /100)
  
  dispatch( {
    type: CART_ADD_ITEM,
    payload: {
      product: data._id,
      name: data.name,
      image: data.image,
      price: thePrice,
      countInStock: data.countInStock,
      qty,
     
    },
  } )

  localStorage.setItem( 'cartItems', JSON.stringify( getState().cart.cartItems ) )
}
export const addToPrice = ( id, price ) => async ( dispatch, getState ) => {
  const { data } = await axios.get( `/api/products/${ id }` )

  dispatch( {
    type: CART_ADD_ITEM,
    payload: {
      product: data._id,
      name: data.name,
      image: data.image,
      price,
      countInStock: data.countInStock,
      qty: data.qty,
    },
  } )

  localStorage.setItem( 'cartItems', JSON.stringify( getState().cart.cartItems ) )
}

export const removeFromCart = ( id ) => ( dispatch, getState ) => {
  dispatch( {
    type: CART_REMOVE_ITEM,
    payload: id,
  } )

  localStorage.setItem( 'cartItems', JSON.stringify( getState().cart.cartItems ) )
}

export const saveShippingAddress = ( data ) => ( dispatch ) => {
  dispatch( {
    type: CART_SAVE_SHIPPING_ADDRESS,
    payload: data,
  } )

  localStorage.setItem( 'shippingAddress', JSON.stringify( data ) )
}

export const savePaymentMethod = ( data ) => ( dispatch ) => {
  dispatch( {
    type: CART_SAVE_PAYMENT_METHOD,
    payload: data,
  } )

  localStorage.setItem( 'paymentMethod', JSON.stringify( data ) )
}
